<template>
  <div class="addAddress mescroll">
    <div class="list">
       <div class="item  flex "> 
          <van-cell title="账户类型" :value="bcTypeName" title-class="label" value-class="color"  @click="showBankcard = true"  is-link />  
      </div>
      <div
        v-if="bcTypeValue == '3'"
        class="item flex"
      >
        <div class="label">开户行</div>
        <div class="value flex-2">
          <van-field  v-model="bcName" placeholder="请输入开户行"  clearable />
        </div>
      </div>
      <div class="item flex">
        <div class="label">账户名</div>
        <div class="value flex-2">
          <div>
            <van-field v-model="userBcUsername" placeholder="请输入收款账户名称" clearable />
          </div> 
        </div>
      </div>
      <div class="item item-center flex">
        <div class="label">账 号</div>
        <div class="value flex-2">
          <van-field v-model="userBcNumber" placeholder="请输入收款账号" clearable />
        </div>
      </div>
      <div class="item item-center flex">
        <div class="label">收款码</div>
        <div class="value flex-2">
          <van-field label="">
              <template #input>
                  <van-uploader v-model="headerFileList" max-count="1" :after-read="_read_file_finised" />
              </template>
          </van-field>   
        </div>
      </div> 
      <div class="item item-bottom flex">
        <div class="label">设为默认地址</div>
        <div class="value flex-2">
          <van-switch v-model="checked" active-color="#eb5252" bind:change="onChange" />
        </div>
      </div>
    </div>
    <div class="btnWrap">
      <div class="btn" @click="save">保存</div>
    </div> 
    <van-popup v-model="showBankcard" position="bottom">
      <div class="popup">
        <div class="popup-top">账户类型</div>
        <div class="popup-mid">
          <van-radio-group >
            <van-cell-group > 
              <div  v-for="(item2,index) in bcTypeList" :key="index" class="item">
                  <div  clickable  @click="choosePayType(item2)" class="left" >
                      {{item2?item2.dictLabel: ''}}   
                  </div>
                  <div  clickable  @click="choosePayType(item2)" class="right" >
                    <van-cell >
                      <template #right-icon>
                        <van-radio name="index"/> 
                      </template>
                    </van-cell> 
                  </div>
              </div> 
            </van-cell-group>
          </van-radio-group>
        </div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import { Switch, Field, Popup,Picker,Icon,RadioGroup,Radio,Checkbox  } from "vant";
import { addBankcard, getBankcardDetail, getBctypeList,uploadPic } from "@/api"; 
export default {
  components: {
    [Switch.name]: Switch,
    [Field.name]: Field,
    [Popup.name]: Popup ,
    [Picker.name]: Picker,  
    [Icon.name]: Icon,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    [Checkbox.name]: Checkbox
  },
  data() {
    return {
      id: "",
      bcType: "",
      bcName: "",
      userBcUsername: "",
      userBcNumber: "",
      checked: false, 
      showBankcard: false,
      bcTypeList: {},
      bcTypeName: '支付宝',
      bcTypeValue: '2',
      bcImg: '',
      headerFileList: [{url:''}]  
    };
  },
  methods: { 
    choosePayType(item2) {
      /**
       * 如果账户类型为“微信支付”或“支付宝”则开户行默认填“微信支付”和“支付宝”
       * 如果账户类型为“农业银行”则开户行字段默认填写“银行卡”
       */
      let bcName = '';
      switch(item2.dictValue){
        case '1':
          bcName = '微信支付';
          break;
        case '2':
          bcName = '支付宝';
          break;
        case '3':
        default:
          bcName = '银行卡';
          break;
      }
      this.bcName = bcName;

      this.bcTypeName = item2.dictLabel;
      this.bcTypeValue = item2.dictValue;
      this.bcType = item2.dictValue;
      this.showBankcard = false;
    },
    save() {
      if (!this.bcType) {
        this.$toast("请选择账户类型");
        return;
      }
      if (!this.bcName) {
        this.$toast("请输入开户行");
        return;
      }
      if (!this.userBcUsername) {
        this.$toast("请输入账户姓");
        return;
      }
      if (!this.userBcNumber) {
        this.$toast("请输入账号");
        return;
      }
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      addBankcard({
        bcCode: this.id || undefined,
        bcType: this.bcType,
        bcName: this.bcName,
        userBcUsername: this.userBcUsername,
        userBcNumber: this.userBcNumber,  
        isDefault: this.checked ? 1 : 0,
        bcSort: 1,
        bcImg: this.bcImg
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.$toast(this.id ? "修改成功" : "新增成功");
          setTimeout(() => {
            this.$router.back();
          });
        } else {
          this.$toast(res.msg);
        }
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getBankcardDetail({ id: this.$route.query.id }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.id = res.data.bcCode;
          this.bcType = res.data.bcType;
          this.bcTypeName = res.data.bcTypeName;
          this.bcTypeValue = res.data.bcType;
          this.bcName = res.data.bcName;
          this.userBcUsername = res.data.userBcUsername;
          this.userBcNumber = res.data.userBcNumber; 
          this.bcImg = res.data.bcImg; 
          this.checked = res.data.isDefault == 1 ? true : false; 
          this.headerFileList = [{
                            url:  res.data.bcImg?this.$utils.dealImg(res.data.bcImg):''
                        }]
        } else {
          this.$toast(res.msg);
        }
      }); 
    },
    loadBctype()
    {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getBctypeList().then(res => {
          loading.clear();
          if(res.code == 0)
          {
            this.bcTypeList = res.data;
          }else {
          this.$toast(res.msg);
        }
      })
    },
    _read_file_finised(e){
        let formData = new FormData();
        let config = {
            headers: { "Content-Type": "multipart/form-data" },
        };
        formData.append("file", e.file);
        formData.append("path", 1);
        uploadPic(formData, config).then(res => {
            if (res.code == 0) {
                this.bcImg = res.data.filename; 
            } else {
                this.$toast.fail("图片上传失败！");
            }
        });
    },
  },
  mounted() {
    document.title = this.$route.query.id ? "修改收款账号" : "新增收款账号";
    this.loadBctype();
    if (this.$route.query.id) {
      this.loadData();
    }
   
  }
};
</script>
<style lang="scss" scoped>
.addAddress {
  background: #fff;
  .list {
    padding: 0 15px;
    .flex {
      display: flex;
    }
    .item {
      padding: 15px 0;
      border-bottom: 1pt solid #efefef;
      .flex-2 {
        flex: 2;
      }

      .label {
        font-size: 15px;
        line-height: 30px;
        font-weight: bold;
        margin-right: 15px;
        width: 60px;
      }
      .value {
        input {
          height: 30px;
          font-size: 15px;
          color: #666;
          width: 100%;
        }
        textarea {
          width: 100%;
          height: 30px;
          font-size: 15px;
          color: #666;
          margin-top: 7px;
        }
      }

      .value {
        .prompt {
          color: #999;
          font-size: 12px;
          line-height: 20px;
          span {
            color: $base-color;
          }
        }
      }
    }
    .area {
      .value {
        font-size: 15px;
        color: #666;
      }
      img {
        width: 5px;
      }
    }
    .item-center {
      align-items: center;
    }
    .item-bottom {
      .label {
        width: 100px;
        line-height: 37px;
      }
      .value {
        text-align: right;
        margin: 3.5px 0 0;
      }
    }
  }
  .btnWrap {
    background: #fff;
    margin: 20px 0 0;
    width: calc(100% - 30px);
    padding: 0 15px 10px;
    .btn {
      background: $base-color;
      color: #fff;
      font-size: 18px;
      width: 100%;
      text-align: center;
      height: 50px;
      line-height: 50px;
      border-radius: 5px;
    }
  }
} 
</style>
<style lang="scss">
.addAddress {
  .van-picker__cancel,
  .van-picker__confirm {
    color: #1989fa !important;
  }
  .van-cell {
    padding: 5px 0;
  }
}
  .popup {
     background: #fff;
    .popup-top {
      line-height: 30px;
      padding: 10px 0;
      text-align: center;
    }
    .popup-mid {
      padding: 0 0 30px; 
    }
    .item{
        width:96%;
        height: 40px;
        line-height: 40px;
        border-bottom:1px solid #f1f1f1;
        margin:0 auto;
        .left{
          width: 90%;
          float: left;
        }
        .right{
          width: 9%;
          float: left;
          padding-top: 5px;
        }
    }
    .van-radio--on {
      background: #ff0000;
    }
  } 
</style>
